<template>
  <li>
    <slot></slot>
  </li>
</template>

<script>
  export default {
    name: 'marquee-item',
    mounted () {
      this.$nextTick(() => {
        setTimeout(() => {
          this.$parent.destroy()
          this.$parent.init()
          this.$parent.start()
        }, 20)
      })
    }
  }
</script>



