<template>
  <div>
    <button @click="test">弹窗</button>
    <button @click="ToastInit">弹窗自定义</button>
  </div>
</template>

<script>
  import Toast from './toast/js/toast.js'

  export default {
    name: 'toast-test',
    methods: {
      test () {
        Toast('这是一个Toast')
      },
      ToastInit () {
        Toast({
          message: '有位置的设定和时间设定',
          position: 'bottom',
          duration: '3000'
        })
      }
    }
  }
</script>

<style scoped>

</style>



