<template>
  <div>
    <ul>
      <li v-for='(item,index) in message' @click='item.isred = !item.isred' :class='{ red : item.isred }'>
        {{item.name}}
      </li>
    </ul>
    <button :disabled='allchoose'>必须全选才可以提交</button>
  </div>
</template>

<script>
  export default {
    created () {
      let data = [
        {name: '混无霹雳手帅不？'},
        {name: '混无霹雳手是老司机不？'},
        {name: '混无霹雳手分享的好不好？'}
      ]
      for (let i of data) {
        this.$set(i, 'isred', false)
      }
      // for (var i = 0; i < data.length; i++) {
      //   console.log(data.length)
      //   data[i].isred = false
      // }
      this.message = data
    },
    data () {
      return {
        message: []
      }
    },
    computed: {
      allchoose () {
        for (let i of this.message) {
          if (i.isred === false) {
            return true
          }
        }
        return false
      }
    }
  }
</script>


<style>
  .red {
    background: red;
  }
</style>



