<docs>
  ## 这是一个示例组件.
</docs>

<template>
  <div class="hello">
    <spinner :type='0' color='#000'></spinner>
    <spinner :type='1' color='#000'></spinner>
    <toast></toast>
    <button @click="add">点击</button>
    <time-down @time-end="message='时间结束'" :endTime="endTime"></time-down>
    <p>{{message}}</p>
    <class-bind></class-bind>
    <marquee direction='down'>
      <marquee-item v-for="item in number" :key="item">当前为{{item}}</marquee-item>
    </marquee>
  </div>
</template>

<script>
  import axios from 'axios'
  import Spinner from './spinner/src/spinner'
  import Toast from './ToastTest'
  import TimeDown from './TimeDown'
  import classBind from './classBind'
  import Marquee from './marquee/marquee'
  import MarqueeItem from './marquee/marquee-item'
  import {getQueryString} from '../base'

  export default {
    data () {
      return {
        message: '正在倒计时',
        endTime: '2017-12-13 12:12:12',
        number: 10
      }
    },
    created () {
      const keyCode = sessionStorage.keyCode = getQueryString('keyCode')
      const keyWord = sessionStorage.keyWord = keyCode.split('_')[0]
      const hunterCode = sessionStorage.hunterCode = keyCode.split('_')[1]
      const sid = sessionStorage.sid = getQueryString('sid')
      const ck = sessionStorage.ck = getQueryString('ck')
      const tm = sessionStorage.tm = getQueryString('tm')
      console.log(this.$store)
      this.$store.commit('getParam', {
        keyCode,
        keyWord,
        hunterCode,
        sid,
        ck,
        tm
      })
    },
    components: {
      Spinner,
      Toast,
      TimeDown,
      classBind,
      Marquee,
      MarqueeItem
    },
    methods: {
      add () {
        axios.get('/api', {
          params: {
            // g_tk: 1645488025,
            // uin: 928128481,
            // format: 'json',
            // inCharset: 'utf-8',
            // outCharset: 'utf-8',
            // notice: 0,
            // platform: 'h5',
            // needNewCode: 1
            channel: 'singer',
            page: 'list',
            key: 'all_all_all',
            pagesize: 100,
            pagenum: 1,
            g_tk: 5381,
            // jsonpCallback: 'GetSingerListCallback',
            loginUin: 0,
            hostUin: 0,
            format: 'json',
            inCharset: 'utf8',
            outCharset: 'utf-8',
            notice: 0,
            platform: 'yqq',
            needNewCode: 0
          }
        })
          .then((response) => {
            console.log(response.data.data.list)
          })
          .catch((error) => {
            console.log(error)
          })
      }
    }
  }
</script>

<style scoped>
  .hello {
    font-size: 0.3rem;
  }
</style>



