<template>
  <transition name='leaveBg'>
    <div class="toast-bg" v-show='visible' @touchend="isVisible" @touchmove.prevent>
      <transition name='fade'>
        <div v-show='visible' :class='position' class='toast'>
          <i>{{message}}</i>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        message: '',
        position: ''
      }
    },
    methods: {
      isVisible () {
        this.close()
      }
    }
  }
</script>


<style>
  .toast-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9998;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
  }

  .toast {
    position: fixed;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    word-wrap: break-word;
    padding: 10px;
    text-align: center;
    z-index: 9999;
    font-size: .3rem;
    max-width: 80%;
    color: #fff;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    overflow: hidden;
  }

  .toast i {
    font-style: normal;
  }

  .toast.middle {
    top: 50%;
  }

  .toast.top {
    top: 10%;
  }

  .toast.bottom {
    top: 90%;
  }

  .leaveBg-enter-active, .leaveBg-leave-active {
    transition: opacity .5s
  }

  .leaveBg-enter, .leaveBg-leave-active {
    opacity: 0;
  }

  .fade-enter-active, .fade-leave-active {
    transition: transform .5s
  }

  .fade-enter, .fade-leave-active {
    transform: translate(-50%, -50%) scale(0);
  }
</style>



